import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addTagToTask } from '../actions'

class Task extends React.Component {

	constructor( props ) {
		super( props )

		this.state = {
			done: false,
			tags:this.props.tags,
			tagsList:[]

		}
		// this.handleTags = this.handleTags.bind(this)
		this.handleChange = this.handleChange.bind(this)
	}

	noReload(e){
		e.preventDefault()
	}

	addTag(e){
		e.preventDefault()
		this.props.addTagToTask(this.state.tagsList)
	}

	handleChange() {
		this.setState({
			done: !this.state.done,
		})
	}

	render() {

		let taskClass
		this.state.done ? taskClass = 'strike' : taskClass = ''

		return (
			<li className={taskClass}>
				<form onSubmit={this.noReload}>
					<label>
						Add tags
						<select value={optionState}>
							{this.props.tags.map((tag,i)=>{
								return <option key={i} value={i} >{tag}</option>
							})}
						</select>
						<button onSubmit={this.addTag} >Add tag</button>
					</label>
				</form>
				<ul>
					{this.state.tagsList.map((i,tag)=>{return <li key={i}><button>{tag}</button></li>})}
				</ul>
				<br>
				</br>
				<h4>{this.props.task.title}</h4>
				<h5 id="objective">Objective:</h5>
				<i>{this.props.task.goal}</i>
				<input type="checkbox" onChange={this.handleChange} />


			</li>
		)
	}
}

Task.propTypes = {
	task: PropTypes.object.isRequired,
	tags: PropTypes.object,
	addTagToTask: PropTypes.func
}


export default connect(
	state => ({
		tasks: state.tasks,
		tags: state.tags,
		tagsList :state.tagsList
	}), {
		addTagToTask,
	}
)(Task)



